<template>
  <div class="playlistdetail">
    <div class="play-top">
      <img :src="playlistdetail.coverImgUrl" alt="" />
      <div class="center">
        <img class="coverImgUrl" :src="playlistdetail.coverImgUrl" alt="" />
        <div class="play-name">
          <div class="title">{{ playlistdetail.name }}</div>
          <div class="nickname">
            <img
              class="nick-img"
              :src="playlistdetail.creator.avatarUrl"
              alt=""
              v-if="playlistdetail != ''"
            />
            <span v-if="playlistdetail != ''">{{
              playlistdetail.creator.nickname
            }}</span>
          </div>
          <div class="introduction">暂无简介</div>
        </div>
      </div>
    </div>
    <div class="play-list">
      <van-cell v-for="(item, index) in songs" :key="index">
        <!-- 使用 title 插槽来自定义标题 -->
        <template #title>
          <div class="custom-title">{{ index + 1 }}</div>
          <div class="music-name">
            <div class="music-top">{{ item.name }}{{ item.alia[0] }}</div>
            <div class="music-bottom">
              {{ item.ar[0].name }}-{{ item.al.name }}
            </div>
          </div>
        </template>
        <template #right-icon>
          <van-icon name="play-circle-o" />
        </template>
      </van-cell>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: '',
      name: '',
      playlistdetail: [],
      list: [],
      songs: []
    }
  },
  created () {
    this.id = this.$route.query.id
    this.name = this.$route.query.name
    document.title = this.name
    this.playlist()
  },
  mounted () {

  },
  methods: {
    playlist () {
      let data = {
        id: this.id
      }
      this.api.playlistdetail(data).then(res => {
        if (res.data.code == 200) {
          this.playlistdetail = res.data.playlist
          res.data.playlist.trackIds.forEach(item => {
            this.list.push(item.id)
          });
          this.detail()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    detail () {
      let data = {
        ids: this.list.join(',')
      }
      this.api.detail(data).then(res => {
        if (res.data.code == 200) {
          this.songs = res.data.songs
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.playlistdetail {
  margin: 0 0 60px 0;

  .play-top {
    height: 400px;
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.05);
    border-radius: 0 0 35px 35px;
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: blur(20px) contrast(0.9);
    }

    .center {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      display: flex;
      align-items: center;
      justify-content: space-evenly;

      .coverImgUrl {
        width: 250px;
        height: 250px;
        object-fit: fill;
        filter: blur(0) contrast(1) !important;
        border-radius: 30px;
      }

      .play-name {
        width: 50%;
        .title {
          width: 100%;
          font-size: 30px;
          color: #fff;
          font-weight: bold;
          text-align: left;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }

        .nickname {
          width: 100%;
          display: flex;
          align-items: center;
          margin-top: 45px;

          .nick-img {
            width: 40px;
            height: 40px;
            object-fit: fill;
            filter: blur(0) contrast(1) !important;
            border-radius: 50%;
          }

          span {
            font-size: 24px;
            color: #fff;
            margin-left: 20px;
          }
        }

        .introduction {
          font-size: 24px;
          color: #fff;
          text-align: left;
          margin-top: 45px;
        }
      }
    }
  }

  .play-list {
    padding: 20px 0 0 0;
    margin-top: 40px;
    background: #fff;
    border-radius: 20px 20px 0 0;
    box-shadow: 4px 4px 8px 8px rgba(0, 0, 0, 0.05);

    .music-name {
      width: 90%;
      font-size: 22px;

      .music-top {
        font-size: 24px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
      }

      .music-bottom {
        font-size: 22px;
        color: rgba(0, 0, 0, 0.5);
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        margin-top: 10px;
      }
    }
    /deep/ .van-cell {
      padding: 15px 30px;
    }
    /deep/ .van-cell__title {
      text-align: left;
      display: flex;
      flex-wrap: wrap;
    }
    /deep/ .custom-title {
      font-size: 24px;
      width: 10%;
      display: flex;
      align-items: center;
    }
    /deep/ .van-icon {
      display: flex;
      align-items: center;
    }
  }
}
</style>